import { FormGroup, FormControl } from '@angular/forms';

import { Component , OnInit , ViewChild , ElementRef} from '@angular/core';
import {AlertController, ModalController ,  IonRouterOutlet} from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import {  Router , NavigationExtras, ActivatedRoute} from '@angular/router';
import { AuthenticationService } from '../../services/Authentication.service';
import { WebdatarocksComponent } from '../../../webdatarocks/webdatarocks.component';

@Component({
  selector: 'app-pivote-list',
  templateUrl: './pivote-list.page.html',
  styleUrls: ['./pivote-list.page.scss'],
})
export class PivoteListPage implements OnInit {
  @ViewChild('pivot1') child: WebdatarocksComponent;

  items: any  = [] ;
  tituloxx: any;
userinfo: any = [];
moneda; any;
seguro ='';
msg = '';
role: any;
  onPivotReady(pivot: WebDataRocks.Pivot): void {
    console.log('[ready] WebdatarocksComponent', this.child);
  }

  onCustomizeCell(
    cell: WebDataRocks.CellBuilder,
    data: WebDataRocks.CellData
  ): void {
    if (data.isClassicTotalRow) {
      cell.addClass('fm-total-classic-r');
    }
    if (data.isGrandTotalRow) {
      cell.addClass('fm-grand-total-r');
    }
    if (data.isGrandTotalColumn) {
      cell.addClass('fm-grand-total-c');
    }
  }
  customizeToolbar(toolbar) {
    var tabs = toolbar.getTabs(); // get all tabs from the toolbar
    toolbar.getTabs = function() {
        delete tabs[0]; // delete the first tab
        delete tabs[1]; // delete the first tab
        delete tabs[2]; // delete the first tab
        delete tabs[7]; // delete the first tab
        return tabs;
    }
  }
  onReportComplete0(): void {
  //  this.tituloxx = this.router.getCurrentNavigation().extras.state.title;
    switch ( this.tituloxx ) {
      case 'Cobros del dia bus':
        this.authenticationService.getReg('cobrobus/' +  this.userinfo[0].idempresa._id + '/' +  this.userinfo[0].idasociado + '/'+  this.userinfo[0].idsucursal + '/' +  this.userinfo[0].iddispositivo + '/6', 'suscriptorsaldos', 0).then((data) => {
        this.items =  data;
        this.onReportComplete()
       
  }, (err) => {
    this.authenticationService.presentAlert(err.error , this.msg , '');
  });
        break;
    }
  }

  submitForm()
  {
    console.log(this.child.webDataRocks.getColumns())
  }
  onReportComplete(): void {

  
    this.child.webDataRocks.off('reportcomplete');
    this.child.webDataRocks.setReport({
      dataSource: {
        data: this.items
      },
      formats: [{
        name: "monto",
        currencySymbol: this.moneda,
        currencySymbolAlign: "left",
        thousandsSeparator: ",",
        decimalPlaces: 2
      }],
      
      "slice": {
        "rows": [
            {
                "uniqueName": "fecha"
            },
            {
                "uniqueName": "codigo1"
            }
        ],
        "columns": [
            {
                "uniqueName": "Measures"
            }
        ],
        "measures": [
          {
            "uniqueName": "cantidad",
            "aggregation": "sum"
        },
            {
                "uniqueName": "monto",
                "aggregation": "sum",
                "format": "currency"
            }
            
           
        ],
        "expands": {
          "expandAll": true
        }
    },
    
      localization: "https://cdn.webdatarocks.com/loc/es.json"
    });
  }
//../../../../assets/csspivote/loc/es.json
  constructor( private router: Router, private authenticationService: AuthenticationService,
    public alertController: AlertController,  
       public translateService: TranslateService    ,    private modalController: ModalController) { 


console.log('entra')

       }

  ngOnInit() {
    
    this.userinfo = this.authenticationService.getcopiauser();

    this.moneda = this.userinfo[0].idempresa.moneda;
    this.role = this.userinfo[0].role;
    this.tituloxx = this.router.getCurrentNavigation().extras.state.title;
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value  });
    this.translateService.get('MSGM').subscribe((value) => {   this.msg = value  });

  }

}
